import SpiceyLink from './SpiceyLink'
import SubTitle from './SubTitle'
import NormalText from './NormalText'
export default function Intro(){
    return(
        <>
            <SubTitle level={1}>Americans and the internet</SubTitle>
            <NormalText>
                If you are reading this, then you probably understand what the internet is and how to use it. Your not alone in this, according to the Pew Research Center, <SpiceyLink href="https://www.pewresearch.org/internet/fact-sheet/internet-broadband/">90% of Americans use the internet in 2019</SpiceyLink>. Perhaps most relevant to the issue, the Pew Research Center also found that in 2018 <SpiceyLink href="https://www.pewresearch.org/fact-tank/2019/09/11/key-findings-about-the-online-news-landscape-in-america/">“one-in-five adults said they often get news on social media.”</SpiceyLink> Both these statistics have risen from previous years and a trend is clear, the internet is playing a increasingly large role in the lives of Americans.
            </NormalText>
            <SubTitle>The issue</SubTitle>
            <NormalText>
                The internet is being used. What's the big deal? The internet is still relatively new and the legal system is not currently equipped to handle free speech violations. Free speech regulations were created in a time where all impactful speech was done physically in public places. The only thing with the means to censor an individual was the government. Today, things have changed. We now gather on publicly accessible websites to express our views. Unlike their physical counterparts, these websites are privately owned and immune to most free speech protections.
            </NormalText>

            <SubTitle level={1}>Free speech concerns:</SubTitle>
            <SubTitle level={4}>Community Guideline Violations</SubTitle>
            <NormalText>
                Community Guidelines detail the types of content that can be posted to a given site. As these sites are private, they can pick and choose exactly what kind of content they will allow their users to share. These companies can update their community guidelines at any time and censor any kind of speech for violations.
            </NormalText>
            <SubTitle level={4}><kbd>“Shadow Banning”</kbd></SubTitle>
            <NormalText>
                You may have heard the term “shadow ban” <SpiceyLink href="https://www.cnet.com/news/shadow-banning-what-it-is-and-what-its-not/" noIFrame text="CNN article about shadow banning. Specifically, Trump's claims that Twitter was shadow banning republicans.">recently</SpiceyLink>. A shadow ban is a relatively new form of censorship that has arisen from social media sites. The basic idea is that your content won't be shown to an audience. The content will still exist on site, it simply won't be recommended to other users, even those who follow/subscribe to you. This all happens algorithmically so it is very difficult to prove this or rectify. Sites often shadow ban people who share views that the company does not want to be associated with or that the company does not approve of.
            </NormalText>
            <SubTitle level={2}>Bipartisan agreement</SubTitle>
            <NormalText>
                It’s rare to find an issue that both democrats and republicans can agree on. Internet censorship is one of those issues. Both political parties are calling for some kind of reform. [maybe: example from trump and biden]
            </NormalText>
            <SubTitle level={2}>Who should do something about it?</SubTitle>
            <SubTitle level={4}>The people</SubTitle>
            <NormalText>
                Freedom of speech should not be expanded to social media sites. It is not a realistic possibility.
            </NormalText>
            <SubTitle level={4}>The companies</SubTitle>
            <NormalText>
                We need social media giants to institute policies that protect speech on their platforms. 
            </NormalText>
            <SubTitle level={4}>The government</SubTitle>
            <NormalText>
                We need regulation explicitly granting freedom of speech protections to users on social media sites.
            </NormalText>
        </>
    )
}