import ReactTooltip from 'react-tooltip';
import { Route, Link, BrowserRouter as Router } from 'react-router-dom'  

export default function Header() {
    return(
        <nav class="navbar navbar-expand-lg navbar-light fixed-top" id="mainNav">
          <div class="container">
            <a class="navbar-brand" href="index.html">Start Bootstrap</a>
            <button class="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
              Menu
              <i class="fas fa-bars"></i>
            </button>
            <div class="collapse navbar-collapse" id="navbarResponsive">
              <ul class="navbar-nav ml-auto">
                <li class="nav-item">
                    <Link to="/" data-tip="Background info + intro to main viewpoints" class="nav-link">Intro</Link>
                </li>
                <li class="nav-item">
                    <Link to="/the-people" class="nav-link" data-tip="People need to demand better protection on social media">The People</Link>
                </li>
                <li class="nav-item">
                    <Link to="/the-companies" class="nav-link" data-tip="Social media companies must take action">The Companies</Link>
                </li>
                <li class="nav-item">
                    <Link to="/the-government" class="nav-link" data-tip="The government must take action">The Government</Link>
                </li>
                <li class="nav-item">
                    <Link to="/further-reading" class="nav-link" data-tip="Sources you can read for more info">Further Reading</Link>
                </li>
              </ul>
            </div>
          </div>
          <ReactTooltip backgroundColor="blue"/>
        </nav>
    )
}